import { useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { supabase } from '../utils/supabaseClient';

const Callback = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const token = searchParams.get('access_token'); // Obtiene el token de la URL

        if (token) {
          // Utiliza el token para autenticar con Supabase
          const { error } = await supabase.auth.signInWithOAuth({
            provider: 'google',
            options: {
              providerToken: token, // Se enva el token de acceso
            },
          });

          if (error) {
            console.error('Error al iniciar sesin con Supabase:', error.message);
            navigate('/error'); // Redirige a la pgina de error
          } else {
            navigate('/main'); // Redirige a la pgina principal
          }
        } else {
          console.error('No se encontr el token de acceso.');
          navigate('/login'); // Redirige de vuelta al login
        }
      } catch (error) {
        console.error('Error al obtener el usuario:', error.message);
        navigate('/error');
      }
    };

    fetchUser();
  }, [navigate, searchParams]);

  return <div>Procesando autenticacin...</div>;
};

export default Callback;