import React from 'react';
import { supabase } from '../utils/supabaseClient'; // Importa tu cliente de Supabase
import { useNavigate } from 'react-router-dom';

const Login = () => {
    const navigate = useNavigate();

    const handleLogin = async () => {
        try {
            const { error } = await supabase.auth.signInWithOAuth({
                provider: 'google',
                 redirectTo: `${window.location.origin}/Nutriia_Electron/`
            });

            if (error) {
                console.error('Error al iniciar sesin:', error.message);
            } else {
                // Redirigir a la pgina principal o verificar sesin
                navigate(`${window.location.origin}/Nutriia_Electron/`); // Puedes usar '/main' o '/' segn tu configuracin
            }
        } catch (error) {
            console.error('Error al abrir la ventana de autenticacin:', error);
        }
    };

    // Opcional: Funcin para verificar la sesin (puedes usarla para la redireccin al inicio)
    const verificarSesion = async () => {
        const { data: { user }, error } = await supabase.auth.getUser();
        if (error) {
            console.error('Error al verificar la sesin:', error.message);
        } else if (user) {
            navigate(`${window.location.origin}/Nutriia_Electron/`); // Redirige a la pgina principal si hay un usuario
        }
    };

    return (
        <div className="flex items-center justify-center h-full bg-gray-400">
            <div className="bg-white p-6 rounded shadow-md">
                <h2 className="text-2xl mb-4">Iniciar sesin</h2>
                <button
                    onClick={handleLogin}
                    className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600 focus:outline-none"
                >
                    Iniciar sesin con Google
                </button>
            </div>
        </div>
    );
};

export default Login;